import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/views/Login'
import { Message } from 'element-ui'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/login'
  },
  {
    path: '/login',
    component: Login
  },
  {
    path: '/admin',
    redirect: '/admin/index',
    component: () => import('@/views/Admin'),
    children: [
      {
        path: '/admin/index',
        redirect: '/admin/index-one',
        component: () => import('@/components/Index0/Index'),
        children: [
          {
            path: '/admin/index-one',
            component: () => import('@/components/Index0/one')
          },
          {
            path: '/admin/index-two',
            component: () => import('@/components/Index0/two')
          }
        ]
      },
      {
        path: '/admin/index1',
        redirect: '/admin/index1-one',
        component: () => import('@/components/Index1/Index1'),
        children: [
          {
            path: '/admin/index1-one',
            component: () => import('@/components/Index1/one')
          }
        ]
      },
      {
        path: '/admin/index2',
        redirect: '/admin/index2-one',
        component: () => import('@/components/Index2/Index2'),
        children: [
          {
            path: '/admin/index2-one',
            component: () => import('@/components/Index2/one')
          }
        ]
      },
      {
        path: '/admin/index3',
        redirect: '/admin/index3-one',
        component: () => import('@/components/Index3/Index3'),
        children: [
          {
            path: '/admin/index3-one',
            component: () => import('@/components/Index3/one')
          }
        ]
      },
      {
        path: '/admin/index4',
        redirect: '/admin/index4-one',
        component: () => import('@/components/Index4/Index4'),
        children: [
          {
            path: '/admin/index4-one',
            component: () => import('@/components/Index4/one')
          }
        ]
      },
      {
        path: '/admin/index5',
        redirect: '/admin/index5-one',
        component: () => import('@/components/Index5/Index5'),
        children: [
          {
            path: '/admin/index5-one',
            component: () => import('@/components/Index5/one')
          }
        ]
      }
    ]
  },
  {
    path: '*',
    component: () => import('@/components/Notfound/Notfound')
  }
]

const router = new VueRouter({
  routes,
  mode: 'history'
})
// 解决vue-router在3.0版本以上重复点菜单报错问题
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function (location) {
  return originalPush.call(this, location).catch(err => err)
}
// 路由前置守卫
router.beforeEach((to, from, next) => {
  if (!window.sessionStorage.getItem('login')) {
    if (to.path.slice(1, 6) === 'admin') {
      Message({
        message: '请先登录',
        type: 'warning'
      })
      next('/login')
    } else {
      next()
    }
  }
  next()
})

export default router
