<template>
  <div class="container">
    <el-container>
      <el-main>
        <el-button type="primary" @click="login">登录</el-button>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      list: {
        current: 1,
        size: 20,
        projectType: ['科技类', '人才类'],
        industryCategory: ['房地产页', '金融业', '批发和临售业']
      }
    }
  },
  async mounted () {
    const { data: res } = await axios.post('http://preview.dumingtek.com/api/pkgs/rest/project/condition', this.list)
    console.log(res)
    axios.post('http://preview.dumingtek.com/api/pkgs/rest/project/condition', this.list).then(({ data }) => {
      console.log(data)
    })
  },
  methods: {
    login () {
      window.sessionStorage.setItem('login', 'hello')
      this.$router.push('/admin')
      this.$message({
        message: '恭喜你，登录成功',
        type: 'success'
      })
    }
  }
}
</script>

